.content.vertical {
    

    .card-list {
        display: flex;
        gap: 1rem;
        flex-wrap: wrap;
    }

    .content-card {
        width: unset;
        height: 15rem;
        aspect-ratio: 11 / 16;

        background-size: auto 120%;
        background-repeat: no-repeat;
        

        justify-content: flex-end;


        .card-info {
            position: relative;
            font-size: 1.2rem;
            font-weight: 600;
            z-index: 11;
            opacity: 1;
            transform: unset;
            transition: opacity 300ms, transform 300ms;
            

            &::after {
                content: '';
                width: calc(100% * .2);
                height: 2.5px;
                background: rgba(255, 255, 255, 0.74);
                position: absolute;
                top: -.2rem;
                left: 0;
                border-radius: 10px;
            }
        }

        &::after {
            background: linear-gradient(to top, black, transparent);
            opacity: 1;
        }

        &:hover {
            background-size: auto 130%;

            .card-info, &::after {
                opacity: 1;
            }

            .card-info {
                transform: translateX(0px);
            }
        }
    }
}