

:root {
  --background: #0E0E0E;
  --sub-background: #161616;
  --sub-outline: #1C1C1C;

  --accent: #FFA452;
  --light-accent: #FFA452;
  --sub-accent: #5297FF;

  --text: #FFFFFF;
}

html, body {
  padding: 0;
  margin: 0;
}


body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
